<template>
  <table>
    <thead>
    <tr>
      <th>Laenusumma</th>
      <th>Periood</th>
      <th>Alguskuupäev</th>
      <!-- Add more columns as needed -->
    </tr>
    </thead>
    <tbody>
    <tr v-for="loan in loans" :key="loan.id">
      <td>{{ loan.laenusumma }}</td>
      <td>{{ loan.perioodKuudes }}</td>
      <td>{{ loan.alguskuupäev }}</td>
      <!-- Add more cells as needed -->
    </tr>
    </tbody>
  </table>
</template>

<script>
// LoanTable.vue

import { ref, onMounted, watch } from 'vue';
import axios from 'axios';

export default {
  props: ['onNewLoan'],
  setup(props) {
    const loans = ref([]);

    const fetchLoans = async () => {
      try {
        const response = await axios.get('/api/loans'); // Replace with your actual API endpoint
        loans.value = response.data;
      } catch (error) {
        console.error('Failed to fetch loans:', error);
      }
    };

    watch(props.onNewLoan, (newLoan) => {
      if (newLoan) {
        loans.value.push(newLoan);
      }
    });

    onMounted(fetchLoans);

    return {
      loans,
    };
  },
};
</script>